import React, { Component } from 'react';
import { Divider, Icon } from 'antd';
import moment from 'moment';
import isEmpty from 'lodash/isEmpty';
import throttle from 'lodash/throttle';
import Router, { RouterProps } from 'next/router';
import Helmet from 'react-helmet';
import Adsense from 'react-adsense';
import SwipeableViews from 'react-swipeable-views';
import ContentLoader from 'react-content-loader';
import MediaZoom from 'medium-zoom';

import { IPost } from '../types/post';
import { CSSMapper } from '../types/utils';
import ErrorPage from '../components/ErrorPage';
import Tags from '../components/Tags';
import Comments from '../components/Comments';
import AuthorInfo from '../components/AuthorInfo';
import { IAuthor } from '../types/author';
import BackTop from '../components/BackTop';
import Events from '../utils/Events';
import { setMetadatas } from '../utils/Utils';

interface IProps {
    id: string;
    posts: Record<string, IPost>;
    authors: Record<string, IAuthor>;
    setPost?: (args?: any) => void;
    router?: RouterProps;
}

interface IState {
    post: IPost;
    author: IAuthor;
    error: boolean;
}

const styles: CSSMapper = {
    container: { display: 'flex', justifyContent: 'center' },
    viewContainer: { margin: 'auto', maxWidth: 992, flex: 1, width: 'inherit' },
    prevPost: {
        right: 0,
        position: 'fixed',
        marginRight: 32,
        fontSize: '1.25rem',
    },
    nextPost: {
        left: 0,
        position: 'relative',
        marginLeft: 32,
        fontSize: '1.25rem',
    },
    postHeader: {
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        height: '20rem',
        marginBottom: '2rem',
    },
    postCover: {
        backgroundColor: 'rgba(0, 0, 0, 0.5)',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        width: '100%',
        height: '100%',
    },
    title: { fontSize: '2.5em', textAlign: 'center', marginBottom: '2rem', lineHeight: '3.2rem', wordBreak: 'break-word', color: '#fff' },
    createdTime: { fontSize: '1.125em', color: '#ffffffc2' },
    createDate: { marginRight: 8 },
    authorInfo: { margin: '32px 0 32px 0' },
    tags: { display: 'flex', alignItems: 'center', margin: '64px 0 32px 0' },
    tagsIcon: { fontSize: 18, marginRight: 16 },
};

class Post extends Component<IProps, IState> {
    private timeoutId: NodeJS.Timeout;

    state: IState = {
        post: {},
        author: {},
        error: false,
    }

    componentDidMount() {
        this.attachImagesZoomable();
        const content = document.querySelector('.ant-layout-content');
        this.waitForPostContainer(content);
        const { router, posts, authors } = this.props;
        this.getPost(router, posts, authors);
    }

    componentWillReceiveProps(nextProps: IProps) {
        const { router, posts, authors } = nextProps;
        this.getPost(router, posts, authors);
    }

    componentWillUnmount() {
        Events.emit('setpost', {});
    }

    waitForPostContainer = (content) => {
        this.timeoutId = setTimeout(() => {
            if (content) {
                this.attachEvents(content);
                const images = document.querySelectorAll('.blog-markdown img');
        console.log(images, document.querySelectorAll('.blog-markdown'));
                return;
            }
            const queryContent = document.querySelector('.ant-layout-content');
            this.waitForPostContainer(queryContent);
        }, 5);
    }

    attachEvents = (content) => {
        const centerHeight = content.clientHeight / 2;
        const postPrev = document.getElementById('post-prev');
        if (postPrev) {
            postPrev.style.top = `${content.scrollTop + centerHeight}px`;
        }
        const postNext = document.getElementById('post-next');
        if (postNext) {
            postNext.style.top = `${content.scrollTop + centerHeight}px`;
        }
        content.addEventListener('scroll', throttle((e) => {
            const centerHeight = e.target.clientHeight / 2;
            const postPrev = document.getElementById('post-prev');
            if (postPrev) {
                postPrev.style.top = `${e.target.scrollTop + centerHeight}px`;
            }
            const postNext = document.getElementById('post-next');
            if (postNext) {
                postNext.style.top = `${e.target.scrollTop + centerHeight}px`;
            }
        }, 200));
    }

    attachImagesZoomable = () => {
        const images = document.querySelectorAll('.blog-markdown img');
        console.log(images, document.querySelectorAll('.blog-markdown'));
    }

    getPost = (router: any, posts: Record<string, IPost>, authors: Record<string, IAuthor>) => {
        const post = posts[router.query.slug];
        if (post) {
            setMetadatas(post);
            const author = authors[post.author];
            this.setState({
                post,
                author,
            }, () => {
                Events.emit('setpost', post);
            });
        } else {
            if (!isEmpty(posts)) {
                clearTimeout(this.timeoutId);
                this.setState({
                    post: {},
                    error: true,
                });
            }
        }
    }

    handleClickTag = (e: any) => {
        const tag = e.target.textContent;
        Router.push(`/?tag=${tag.trim()}`);
    }

    renderSkeleton = () => {
        return (
            <div style={styles.viewContainer}>
                <ContentLoader
                    speed={2}
                    primaryColor="#f3f3f3"
                    secondaryColor="#ecebeb"
                    height={900}
                    width={400}
                >
                    <rect x="0" y="0" rx="5" ry="5" width="400" height="150" />
                    {Array.from(new Array(30), (v, k) => <rect key={k} y={120 + (k * 30)} rx="0" ry="0" width="400" height="12" />)}
                </ContentLoader>
            </div>
        );
    }

    render() {
        const { error, post, author } = this.state;
        return (
            <div className="container" style={styles.container}>
                <Helmet title={post.title || 'Dev.log'} />
                {
                    error ? (
                        <ErrorPage status={404} />
                    ) : !isEmpty(post) ? (
                        <>
                            <SwipeableViews
                                resistance
                                style={styles.viewContainer}
                                index={post.prev ? 1 : 0}
                                onChangeIndex={(index) => {
                                    if (index === 2) Router.push(post.next);
                                    if (index === 1) Router.push(post.next);
                                    if (index === 0) Router.push(post.prev);
                                }}
                            >
                                {
                                    [post].reduce((prev, curr) => {
                                        if (curr.prev) {
                                            prev.push(
                                                <div key={curr.prev} style={styles.viewContainer}>
                                                    <div id="post-prev" style={styles.prevPost}>
                                                        <Icon type="arrow-left" style={{ marginRight: 4 }} />
                                                        이전 글
                                                    </div>
                                                </div>
                                            );
                                        }
                                        prev.push(
                                            <div key={curr.path} style={styles.viewContainer}>
                                                <div className="post-header" style={{ ...styles.postHeader, backgroundImage: `url(${post.cover || '/static/images/default/no-image.svg'})` }}>
                                                    <div style={styles.postCover}>
                                                        <h1 className="post-title" style={styles.title}>
                                                            {post.title}
                                                        </h1>
                                                        <div style={styles.createdTime}>
                                                            <span style={styles.createDate}>{moment(post.date).format('YYYY.MM.DD')}</span>
                                                            <span>{`(${moment(post.date).fromNow()})`}</span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="blog-markdown">
                                                    <div className="markdown-body" dangerouslySetInnerHTML={{ __html: post.content }} />
                                                </div>
                                                <div style={styles.tags}>
                                                    <Icon type="tags" style={styles.tagsIcon} />
                                                    <Tags tagStyle={{ cursor: 'pointer' }} tags={post.tags ? post.tags.trim().split(',') : []} onClick={this.handleClickTag} />
                                                </div>
                                                {/* <Adsense.Google
                                                    client="ca-pub-8569372752842198"
                                                    slot="7788836161"
                                                    style={{ display: 'block' }}
                                                    format='auto'
                                                    responsive='true'
                                                /> */}
                                                <Divider />
                                                <div style={styles.authorInfo}>
                                                    <AuthorInfo author={author} />
                                                </div>
                                                <Divider />
                                                <Comments />
                                            </div>
                                        );
                                        if (curr.next) {
                                            prev.push(
                                                <div key={curr.next} style={styles.viewContainer}>
                                                    <div id="post-next" style={styles.nextPost}>
                                                        다음 글
                                                        <Icon type="arrow-right" style={{ marginLeft: 4 }} />
                                                    </div>
                                                </div>
                                            );
                                        }
                                        return prev;
                                    }, [])
                                }
                            </SwipeableViews>
                            <BackTop />
                        </>
                    ) : this.renderSkeleton()
                }
            </div>
        );
    }
}

export default Post;
